export default {
  name: "NavBarInfo",
  NavBarList() {
    return {
      massage: "",
      status: 200,
      data: {
        list: NavBarList,
      },
    };
  },
  NavSiderList(req) {
    return {
      massage: "",
      status: 200,
      data: {
        list: getSiderList(JSON.parse(req.body).key),
      },
    };
  },
  NavSearchList() {
    let searchList = [];
    for (const val of NavBarList) {
      if (Array.isArray(val.children)) {
        for (const v of val.children) {
          searchList.push({
            key: v.key,
            title: v.title,
            path: v.path,
            children: getSiderList(v.key),
          });
        }
      }
    }
    return {
      massage: "",
      status: 200,
      data: {
        list: searchList,
      },
    };
  },
};

